* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
}

a {
  text-decoration: none;
}

body {
  padding-right: 20px;
}

button {
  cursor: pointer;
}

img {
  display: block;
  width: 100%;
  height: 100%;
}

li {
  list-style-type: none;
}